
@import "../../global.scss";

.portfolio {
    position: relative;
    @include flexCenter();
    flex-direction: column;
    

    h2 {
        font-size: 5rem;
        min-height: 6rem;
        margin-bottom: 5rem;
    }

    h3 {
        font-size: 3rem;
    }

    ul {
        display: flex;
        width: 60%;
        margin-bottom: 3rem;
        padding: 0;
        list-style: none;
    
    }

    .container {
        width: 90%;
        @include flexCenter();
        flex-wrap: wrap;

        .item {
            position: relative;
            width: 35rem;
            margin: 1rem 2rem;
            border: 1px solid rgba(44, 65, 255, 0.61);
            border-radius: 2rem;
            @include flexCenter();
            color: #ffffff;
            transition: all .5s ease-in-out;
            cursor: pointer;

            h3 {
                position: absolute;
                font-size: 2.5rem;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: rgba(44, 65, 255, 0.904);
                border-radius: 2rem;

                img {
                    opacity: .1;
                    z-index: 0;
                }
                
            }

        }
    }
}